// Helper function to process PowerPoint image data
async function processPowerPointImage(base64Data, fileName) {
  console.log(" Processing PowerPoint image data");
  console.log(" Base64 data length:", base64Data.length);
  
  try {
    // Convert base64 to blob and create a File object
    const byteCharacters = atob(base64Data);
    const byteArrays = [];
    
    console.log(" Converting base64 to byte arrays");
    for (let offset = 0; offset < byteCharacters.length; offset += 512) {
      const slice = byteCharacters.slice(offset, offset + 512);
      const byteNumbers = new Array(slice.length);
      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }
      const byteArray = new Uint8Array(byteNumbers);
      byteArrays.push(byteArray);
    }
    
    const blob = new Blob(byteArrays, { type: 'image/png' });
    const file = new File([blob], fileName, { type: 'image/png' });
    
    console.log(" PowerPoint File object created:", {
      name: file.name,
      size: file.size,
      type: file.type
    });
    
    // Set the uploaded file
    uploadedFile = file;
    capturedImageData = null;
    isImageEditingMode = true;
    
    console.log(" Updating global variables for PowerPoint:", {
      uploadedFile: !!uploadedFile,
      capturedImageData: !!capturedImageData,
      isImageEditingMode: isImageEditingMode
    });
    
    // Update the upload section UI
    console.log(" Calling updateUploadSectionWithFile for PowerPoint");
    updateUploadSectionWithFile(file, `data:image/png;base64,${base64Data}`);
    
    hideLoader();
    showSuccess("Image captured from PowerPoint successfully!");
    console.log(" PowerPoint image processing completed successfully");
    
  } catch (error) {
    console.error(" Error processing PowerPoint image:", error);
    hideLoader();
    showError("Failed to process the captured image. Please try again.");
  }
}

