// Update generateImage function to handle login if not authenticated
async function generateImage() {
  try {
    // Check if user is authenticated
    if (authManager.isTokenExpired()) {
      // If not authenticated, handle login first
      showLoader("Authenticating...", false);
      const authResult = await authManager.authenticate();
      
      if (!authResult.success) {
        hideLoader();
        showError(`Authentication failed: ${authResult.error}`);
        return;
      }
      
      // Authentication successful, continue with image generation or editing
      hideLoader();
      showSuccess("Authentication successful!");
      updateAuthUI(true);
    }
    
    // Now proceed with image generation or editing
    const userId = authManager.getUserId();
    
    if (!userId) {
      showError("User ID not available. Please try logging in again.");
      return;
    }
    
    const isPremium = await checkPremiumStatus(userId);
    const promptText = document.querySelector(".input-field").value.trim();
    
    if (!promptText) {
      if (isImageEditingMode) {
        showError("Please enter instructions for editing the image.");
      } else {
        showError("Please enter a prompt for the image generation.");
      }
      return;
    }

    // Check if we're in image editing mode
    if (isImageEditingMode && (uploadedFile || capturedImageData)) {
      await editImage(userId, isPremium, promptText);
    } else {
      // Original image generation logic
      const purposeSelect = document.getElementById("image-purpose-select");

      if (!purposeSelect.value) {
        showError("Please select an image purpose.");
        return;
      }
      
      await generateNewImage(userId, isPremium, promptText, purposeSelect.value);
    }

  } catch (error) {
    console.error("Detailed error in generateImage:", {
      error: error,
      message: error.message,
      stack: error.stack
    });
    hideLoader();
    
    let errorMessage = "An error occurred while processing the image.";
    if (error.name === 'AbortError') {
      errorMessage = "Image processing was cancelled.";
    } else if (error.message.includes("HTTP error")) {
      errorMessage = "Failed to connect to the image processing service. Please try again later.";
    } else if (error.name === "TypeError" && error.message.includes("fetch")) {
      errorMessage = "Network error. Please check your internet connection.";
    }
    
    showError(errorMessage);
    
    // Reset button state
    const generateButton = document.getElementById("run");
    generateButton.disabled = false;
  }
}

// New function to handle image editing
async function editImage(userId, isPremium, promptText) {
  console.log("Starting image editing process");
  
  // Check tokens before editing
  console.log("Checking tokens before editing for user:", userId);
  
  const tokenResponse = await fetch(`https://shorts.multiplewords.com/api/tokens_left/get/${userId}`, {
    method: "GET"
  });

  console.log("Pre-editing token check status:", tokenResponse.status);

  if (!tokenResponse.ok) {
    throw new Error(`HTTP error! status: ${tokenResponse.status}`);
  }

  const tokenData = await tokenResponse.json();
  console.log("Pre-editing token check response:", tokenData);
  
  console.log("User premium status:", isPremium);
  console.log("Available video tokens:", tokenData.credits?.videos);
  
  if (!isPremium && (!tokenData.credits || !tokenData.credits.videos || tokenData.credits.videos < 1)) {
    console.log("Token check failed:", {
      hasCredits: !!tokenData.credits,
      videoTokens: tokenData.credits?.videos
    });
    showError("Insufficient tokens. Please get more credits to continue.");
    return;
  }

  // Show loader and disable generate button
  showLoader("Editing your image...");
  const generateButton = document.getElementById("run");
  generateButton.disabled = true;

  // Create AbortController for the request
  currentGenerationController = new AbortController();

  // Create FormData for image editing
  const editFormData = new FormData();
  editFormData.append('prompt', promptText);
  editFormData.append('user_id', userId);
  editFormData.append('isPro', isPremium ? '1' : '0');

  // Add the image file to the form data
  if (uploadedFile) {
    editFormData.append('file', uploadedFile);
    console.log("Using uploaded file for editing:", uploadedFile.name);
  } else if (capturedImageData) {
    // Convert captured image data to blob and add to form data
    const blob = await convertImageDataToBlob(capturedImageData);
    editFormData.append('file', blob, 'captured_image.png');
    console.log("Using captured image data for editing");
  } else {
    throw new Error("No image available for editing");
  }

  console.log("Sending image editing request:", {
    isPro: isPremium ? '1' : '0',
    user_id: userId,
    prompt: promptText,
    hasFile: !!(uploadedFile || capturedImageData),
    endpoint: "https://shorts.multiplewords.com/mwvideos/api/edit_image_from_text"
  });

  const response = await fetch("https://shorts.multiplewords.com/mwvideos/api/edit_image_from_text", {
    method: "POST",
    body: editFormData,
    signal: currentGenerationController.signal
  });

  console.log("Image editing response status:", response.status);
  
  if (!response.ok) {
    throw new Error(`HTTP error! status: ${response.status}`);
  }

  const data = await response.json();
  console.log("Image editing full response:", data);

  if (data.status === 1 && data.edited_image_url) {
    console.log("Successfully received edited image URL:", data.edited_image_url);
    // Update loader message
    showLoader("Inserting edited image into document...");
    // Insert the edited image into the document
    await insertImageToDocument(data.edited_image_url);
    hideLoader();
    showSuccess("Image edited and inserted successfully!");
    // Update token count after successful editing
    if (!isPremium) {
      console.log("Updating tokens after editing for non-premium user");
      checkTokens();
    }
  } else {
    hideLoader();
    console.error("Invalid API response:", {
      status: data.status,
      hasEditedImageUrl: !!data.edited_image_url
    });
    const errorMsg = data.msg || data.message || "Failed to edit image. Please try again.";
    showError(errorMsg);
  }

  // Reset button state
  generateButton.disabled = false;
}

